import {Comment as CommentType} from '../../types/comments';
import {calculateRatingStars, getFormattedDate} from '../../utils';

type Props = {
  comment: CommentType
}

function Comment({comment}: Props) {
  const date = new Date(comment.date);

  return (
    <li className="reviews__item">
      <div className="reviews__user user">
        <div className="reviews__avatar-wrapper user__avatar-wrapper">
          <img className="reviews__avatar user__avatar" src={comment.user.avatarUrl} width="54" height="54" alt="Comments avatar"/>
        </div>
        <span className="reviews__user-name">{comment.user.name}</span>
      </div>
      <div className="reviews__info">
        <div className="reviews__rating rating">
          <div className="reviews__stars rating__stars">
            <span style={{width: `${calculateRatingStars(comment.rating)}%`}}/>
            <span className="visually-hidden">Rating</span>
          </div>
        </div>
        <p className="reviews__text">{comment.comment}</p>
        <time className="reviews__time" dateTime={getFormattedDate('YYYY-MM-DD', date)}>{getFormattedDate('MMMM YYYY', date)}</time>
      </div>
    </li>
  );
}

export default Comment;

